<script setup lang="ts">
import { ACol, ACheckbox, ATextField } from "@actindo/core-ui";
import type {
  ITenantAttribute,
  IDataTypeInstance,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import type { IDataTypesImageValueConfiguration } from "./models";

defineProps<{
  attribute: ITenantAttribute;
  dataTypeInstance: IDataTypeInstance<IDataTypesImageValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesImageValueConfiguration>({
  set: ({ maxImages, minImages, ...rest }) =>
    ({
      maxImages: Number(maxImages),
      minImages: Number(minImages),
      ...rest,
    }) as IDataTypesImageValueConfiguration,
});
</script>

<template>
  <ACol>
    <ATextField
      v-model="configuration.prefixField"
      :label="$t('images.prefixField')"
      :readonly="readonly"
      :hint="$t('images.prefixHint')"
      persistent-hint
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.minImages"
      :label="$t('images.minImages')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model.number="configuration.maxImages"
      :label="$t('images.maxImages')"
      type="number"
      :readonly="readonly"
    />
  </ACol>
  <ACol>
    <ATextField
      v-model="configuration.supportedFileTypes"
      :label="$t('images.supportedFileTypes')"
      :readonly="readonly"
      :hint="$t('images.supportedFileTypesHint')"
      persistent-hint
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>
